import { Box, ListItemButton, Typography } from '@mui/material';
import Image from 'mui-image';
import React from 'react';
import { showDateDetail } from 'utils/format-utils';
import dayjs from 'dayjs';
import {HistoryItemData} from '../../history/states/history.types';


export type HistoryProps = {
  item: HistoryItemData;
  beforeItemDate: Date;
  key: number | string;
  onPress?: () => void;
};

export default function HistoryItem({ item, beforeItemDate, onPress }: HistoryProps) {
  const checkShow = (): boolean => {
    if (!beforeItemDate) return true;
    return (
      dayjs(item?.publishedAt).format('DD/MM/YYYY') !==
      dayjs(beforeItemDate).format('DD/MM/YYYY')
    );
  };

  /* eslint-disable-next-line @typescript-eslint/no-unsafe-assignment */
  return (
    <>
      {checkShow() && (
        <Box sx={{ paddingTop: 2, paddingLeft: 2.5, paddingRight: 2.5 }}>
          <Typography variant="subtitle2" color="text.gray" textAlign="left">
            {showDateDetail(item?.publishedAt)}
          </Typography>
        </Box>
      )}
      <ListItemButton
        sx={{
          width: '100%',
          padding: 0,
          paddingTop: 2,
          display: 'flex',
          flexDirection: 'column'
        }}
        onClick={onPress}
      >
        <Box
          sx={{ width: '100%' }}
          pl={2.5}
          pr={2.5}
          mb={2.5}
          display="flex"
          justifyContent="center"
          alignItems="center"
        >
          <Image src="images/icons/withdrawIcon.svg" width={32} height={32} duration={0} />
          <Box sx={{ paddingLeft: 1.5 }} />
          <Box display="flex" flexDirection="column" sx={{ flex: 1 }}>
            <Box sx={{ width: '100%' }} display="flex">
              <Typography variant="h5" sx={{ flex: 1 }}>
                {item?.block?.title ?? ''}
              </Typography>
              <Typography variant="subtitle2" color="text.gray">
                {item?.block?.time}
              </Typography>
            </Box>
            <Box
              sx={{ width: '100%' }}
              display="flex"
              justifyContent="center"
              alignItems="center"
            >
              <Image
                src="images/icons/iconLinkedCard.svg"
                width={12}
                height={12}
                duration={0}
              />
              <Typography
                sx={{
                  flex: 1,
                  fontSize: 12,
                  paddingLeft: 0.5,
                  color: 'text.gray',
                }}
              >
                {item?.block?.paymentMethod}
              </Typography>
              <Typography
                variant="h5"
                fontWeight="600"
                sx={{ color: 'text.secondary' }}
              >
                {item?.block?.total}
              </Typography>
            </Box>
          </Box>
        </Box>
        <Box
          width='calc(100% - 40px)'
          sx={{
            borderBottom: '1px solid',
            borderBottomColor: 'colors.light',
          }}
        />
      </ListItemButton>
    </>
  );
}
