import { Button, Typography } from '@mui/material'
import Modal from 'components/Modal'
import { ModalHandle } from 'components/Modal/types'
import { FlexBox } from 'components/styled'
import React, { forwardRef, useRef, useImperativeHandle, useState } from 'react'
import { useTranslation } from 'react-i18next'
import CalenderButton from './CalenderButton'
import CustomDatePicker, { DatePickerHandle } from './CustomDatePicker'
import PeriodChosen from './PeriodChosen'
import SelectService from './SelectService'

const FilterModal = forwardRef<ModalHandle>((props, ref) => {
  const modalRef = useRef<ModalHandle>(null)
  const { t } = useTranslation()
  const [startDate, setStartDate] = useState<Date>(new Date())
  const [endDate, setEndDate] = useState<Date>(new Date())
  const startDateRef = useRef<DatePickerHandle>(null)
  const endDateRef = useRef<DatePickerHandle>(null)
  const [selected, setSelected] = useState<string>()
  useImperativeHandle(
    ref,
    () => ({
      open: () => modalRef.current?.open?.(),
      close: () => modalRef.current?.close?.()
    })
  )

  const handleSelectDate = () => {
    if(selected === 'startDate'){
      startDateRef.current?.value && setStartDate(startDateRef.current?.value)
      setSelected('')
    }
    if(selected === 'endDate'){
      endDateRef.current?.value && setEndDate(endDateRef.current?.value)
      setSelected('')
    }
  }

  const handleCancel = () => {
    if(selected === 'startDate' || selected === 'endDate') {
      setSelected('')
      return
    }
    //Handle clear filter
  }

  const handleSubmitFilter = () => {

  }

  return (
    <Modal
      ref={modalRef}
      title={t('history.filter')}
      disableDrag={selected === 'startDate' || selected === 'endDate'}
    >
      <FlexBox pt={1} />
      <Typography variant='h4' textAlign='left'>{t('history.filterDate')}</Typography>
      <FlexBox pt={2} />
      <FlexBox>
        <CalenderButton
          id='startDate'
          selectedId={selected}
          date={startDate}
          onClick={() => setSelected('startDate')}
        />
        <FlexBox pl={2.5} />
        <CalenderButton
          id='endDate'
          selectedId={selected}
          date={endDate}
          onClick={() => setSelected('endDate')}
        />
      </FlexBox>
      <FlexBox pt={2} />
      {(selected !== 'startDate' && selected !== 'endDate') && <FlexBox width='100%' flexDirection='column'>
        <PeriodChosen
          selectedId={selected}
          onSelect={(value) => setSelected(value)}
        />
        <FlexBox pt={2} />
        <Typography variant='h4' textAlign='left'>{t('history.serviceType')}</Typography>
        <FlexBox pt={2} />
        <SelectService />
      </FlexBox>}
      <CustomDatePicker
        ref={startDateRef}
        show={selected === 'startDate'}
        defaultValue={startDate}
        maxDate={endDate}
      />
      <CustomDatePicker
        ref={endDateRef}
        show={selected === 'endDate'}
        defaultValue={endDate}
      />
      <FlexBox pt={3} />
      <FlexBox>
        <Button
          fullWidth
          variant="outlined"
          disableElevation
          sx={{ fontSize: 16, fontWeight: '600', color: 'text.primary', height: 48, borderColor: 'colors.gray', borderRadius: 2.5 }}
          onClick={handleCancel}
        >
          {selected === 'startDate' || selected === 'endDate' ? t('history.cancel') : t('history.clear')}
        </Button>
        <FlexBox pr={1} pt={1.5} />
        <Button
          fullWidth
          variant="contained"
          disableElevation
          sx={{ height: 48, borderRadius: 2.5 }}
          onClick={selected === 'startDate' || selected === 'endDate' ? handleSelectDate : handleSubmitFilter}
        >
          {selected === 'startDate' || selected === 'endDate' ? t('history.confirm') : t('history.apply')}
        </Button>
      </FlexBox>
    </Modal>
  )
})

export default FilterModal
