import { Typography } from '@mui/material'
import Image from 'mui-image'
import React from 'react'
import { useTranslation } from 'react-i18next'
import { FlexBox } from './styled'

function StatusBadge({ status }: Props) {
  const { t } = useTranslation()
  return (
    <FlexBox
      justifyContent='center'
      alignItems='center'
      bgcolor={`cash.${status || ''}`}
      p={0.5}
      pl={2}
      pr={2}
      borderRadius={2}
    >
      <Image src={getImage(status)} width={ status === 'pending' ? 15 : 16} duration={0} bgColor='transparent' />
      <FlexBox pl={0.5} />
      <Typography fontSize={12} color={getColor(status)}>{t(`status.${status || 'pending'}`)}</Typography>
    </FlexBox>
  )
}

const getImage = (status?: string): string => {
  switch (status) {
    case 'SUCCEEDED': return 'images/icons/cashSuccessIcon.svg'
    case 'FAILED': return 'images/icons/cashFailedIcon.svg'
    default: return 'images/icons/cashPendingIcon.svg'
  }
}

const getColor = (status?: string) => {
  switch (status) {
    case 'SUCCEEDED': return 'text.secondary'
    case 'FAILED': return 'text.danger'
    case 'PENDING': return 'text.pending'
    default: return ''
  }
}

type Props = {
  status?: string,
}

export default StatusBadge