import { Box, Typography } from '@mui/material';
import React from 'react';

export default function KeyValueRow(props: ItemRowProps) {
  const { kei, value, valueColor, isLast } = props;

  return (
    <Box
      width="100%"
      display="flex"
      justifyContent="space-between"
      borderBottom={!isLast ? '1px solid' : 'none'}
      borderColor="colors.light"
      pt={1.5}
      pb={1.5}
    >
      <Typography variant="subtitle1">{kei || ''}</Typography>
      <Typography textAlign={'right'} variant="subtitle1" fontWeight="600" color={valueColor || 'text.primary'}>
        {value || ''}
      </Typography>
    </Box>
  );
}

type ItemRowProps = {
  kei?: string;
  value?: string;
  valueColor?: string,
  isLast?: boolean
};
