import { Box, Button } from '@mui/material'
import { selectDeviceInfo } from 'app/app.slice'
import { useAppDispatch, useAppSelector } from 'app/hooks'
import alertDialogStore from 'components/AlertDialog/alertDialogStore'
import Header from 'components/Layout/Header'
import { LayoutSafeArea } from 'components/Layout/LayoutSafeArea'
import { LoadingIcon } from 'components/Loading'
import Meta from 'components/Meta'
import { FlexBox, FullSizeCenteredFlexBox, FullSizeFlexBox } from 'components/styled'
import React, { useEffect } from 'react'
import { useTranslation } from 'react-i18next'
import { useNavigate, useSearchParams } from 'react-router-dom'
import CashAdditionalInfo from '../components/CashAdditionalInfo'
import CashInfo from '../components/CashInfo'
import HeaderResult from '../components/HeaderResult'
import { historySagaAction } from '../states/history.saga'
import { clearHistoryDetail, selectHistoryDetails } from '../states/history.slice'
import { CallbackHistoryDetailResponse, HistoryDetailPayload } from '../states/history.types'

function CashResult() {
  const deviceInfo = useAppSelector(selectDeviceInfo)
  const { t } = useTranslation()
  const navigate = useNavigate()
  const [searchParams] = useSearchParams()
  const dispatch = useAppDispatch()
  const transaction = searchParams.get('transaction') || ''
  const serviceType = searchParams.get('serviceType') || ''
  const data = useAppSelector(selectHistoryDetails)

  useEffect(() => {
    getHistoryDetails()
    return () => {
      dispatch(clearHistoryDetail())
    }
  }, [])

  const getHistoryDetails = () => {
    if (data?.transaction?.[0].rightTitle !== transaction)
      dispatch(historySagaAction.getHistoryDetails({ transaction, serviceType } as HistoryDetailPayload,
        (status: boolean, response: CallbackHistoryDetailResponse) => {
          if (status) {
            if (response.code === 202611) {
              alertDialogStore.alertContent = {
                title: 'Lấy thông tin thất bại',
                subtitle: 'Lấy thông tin giao dịch thất bại. Vui lòng thử lại sau',
                firstButtonTitle: 'Thử lại',
                secondButtonTitle: 'Trở về',
                onClickFirstButton: () => getHistoryDetails(),
                onClickSecondButton: () => navigate(-1),
                reverseButton: true
              }
            }
          }
        }))
  }

  const handleFirstButtonClick = () => {
    switch (serviceType) {
      case 'WITHDRAW': {
        navigate('/withdraw', { replace: true })
        return
      }
      case 'TRANSFER': {
        navigate('/transfer', { replace: true })
        return
      }
      case 'DEPOSIT': {
        navigate('/deposit', { replace: true })
        return
      }
      default: {
        return
      }
    }
  }

  return (
    <LayoutSafeArea>
      <Meta title="Kết quả giao dịch" />
      <FullSizeFlexBox
        padding={2.5}
        paddingTop={deviceInfo?.platform?.toUpperCase() === 'IOS' || deviceInfo?.platform?.toUpperCase() === 'ANDROID' ? 0 : 2.5}
        paddingBottom={deviceInfo?.platform?.toUpperCase() === 'IOS' || deviceInfo?.platform?.toUpperCase() === 'ANDROID' ? 0 : 2.5}
        display="flex"
        flexDirection="column"
        justifyContent="space-between"
      >
        {data ?
          <>
            <Box>
              <Header />
              <HeaderResult info={data?.blockInfo?.[0] || {}} status={data?.data.state ?? 'PENDING'} />
              <Box sx={{ paddingTop: 3.5 }} />
              <CashInfo
                transactions={data?.transaction || []}
              />
              {/* <CashAdditionalInfo
            accountName='Viet Hồ'
            accountNumber='0909 123456'
            content='OK'
          /> */}
              <Box sx={{ paddingTop: 2.5 }} />
            </Box>
            <FlexBox mb={2}>
              <Button
                fullWidth
                variant="outlined"
                disableElevation
                sx={{ borderRadius: 2.5, height: 48 }}
                onClick={handleFirstButtonClick}
              >
                {data?.data.state === 'FAILED' ? t('cash-result.tryAgain') : t('cash-result.newTransaction')}
              </Button>
              <Box pr={1} />
              <Button
                fullWidth
                variant="contained"
                disableElevation
                sx={{ borderRadius: 2.5, height: 48 }}
                onClick={() => navigate('/home')}
              >
                {t('cash-result.goToHome')}
              </Button>
            </FlexBox>
          </> :
          <FullSizeCenteredFlexBox><LoadingIcon /></FullSizeCenteredFlexBox>}
      </FullSizeFlexBox>
    </LayoutSafeArea>
  )
}

export default CashResult