import { ButtonBase, Typography } from '@mui/material'
import { FlexBox } from 'components/styled'
import React from 'react'
import { useTranslation } from 'react-i18next'

function PeriodChosen({ selectedId, onSelect }: PeriodChosenProps) {
  const { t } = useTranslation()
  return (
    <FlexBox width='100%'>
      <Item
        id='1week'
        label={t('history.1week')}
        selectedId={selectedId}
        onClick={() => onSelect?.('1week')}
      />
      <FlexBox pl={1} />
      <Item
        id='1month'
        label={t('history.1month')}
        selectedId={selectedId}
        onClick={() => onSelect?.('1month')}
      />
      <FlexBox pl={1} />
      <Item
        id='3month'
        label={t('history.3month')}
        selectedId={selectedId}
        onClick={() => onSelect?.('3month')}
      />
    </FlexBox>
  )
}

const Item = ({ id, selectedId, onClick, label }: ItemProps) => {
  return (
    <ButtonBase
      onClick={onClick}
      sx={{
        display: 'flex',
        flex: 1,
        p: 1.5,
        borderRadius: 2,
        border: '1px solid',
        borderColor: selectedId === id ? 'primary.main' : 'colors.gray',
        backgroundColor: selectedId === id ? 'colors.lightGreen' : 'inherit',
        justifyContent: 'center',
      }}
    >
      <Typography variant='h5'>{label}</Typography>
    </ButtonBase>
  )
}

type PeriodChosenProps = {
  selectedId?: string,
  onSelect?: (value?: string) => void
}

type ItemProps = {
  id?: string,
  selectedId?: string,
  onClick?: () => void,
  label?: string
}

export default PeriodChosen