import React, { forwardRef, useImperativeHandle, useState } from 'react'
import DatePicker, { DateConfig } from 'react-mobile-datepicker-ts';
import 'react-mobile-datepicker-ts/dist/main.css'
import './styles.css'
const CustomDatePicker = forwardRef<DatePickerHandle, React.PropsWithChildren<DatePickerProp>>(
  ({defaultValue , show, maxDate = new Date()}: DatePickerProp, ref) => {
  const [value, setValue] = useState(defaultValue)
    
  useImperativeHandle(
    ref,
    () => ({
      value: value
    })
  )

  const handleOnchange = (e: Date) => {
    setValue(e)
  }
  if(!show) return null
  return (
    <DatePicker
      isOpen={true}
      isPopup={false}
      showCaption={false}
      showHeader={false}
      showFooter={false}
      dateConfig={[{
        type: 'year',
        format: 'YYYY',
        caption: 'Year',
        step: 1,
      }, {
        type: 'month',
        format: 'MM',
        caption: 'Mon',
        step: 1,
      }, {
        type: 'date',
        format: 'DD',
        caption: 'Day',
        step: 1,
      }] as DateConfig[]}
      value={value}
      onChange={handleOnchange}
      max={maxDate}
    />
  )
})

type DatePickerProp ={
  defaultValue?: Date,
  show?: boolean,
  maxDate?: Date
}

export type DatePickerHandle = {
  value?: Date
}

export default CustomDatePicker