import { Box, Typography } from '@mui/material'
import StatusBadge from 'components/StatusBadge'
import { FlexBox } from 'components/styled'
import Image from 'mui-image'
import React from 'react'
import { BlockInfoProp } from '../states/history.types'

function HeaderResult({ info, status }: Props) {
  return (
    <FlexBox flexDirection='column' justifyContent='center' alignItems='center'>
      <Image
        src={getImage(status)}
        duration={100}
        width={100}
        height={100}
      />
      <Box pt={3} />
      <Typography variant='h4'>{info.title ?? ''}</Typography>
      <Box pt={0.5} />
      <Typography
        fontSize={24}
        fontWeight='600'
        // color={balanceStatus === '+' ? 'text.secondary' : 'text.primary'}
      >
        {info.description ?? ''}
      </Typography>
      <Box pt={0.5} />
      <StatusBadge status={status} />
    </FlexBox>
  )
}

const getImage = (status?: string): string => {
  switch (status) {
    case 'SUCCEEDED': return 'images/cashSuccess.svg'
    case 'FAILED': return 'images/cashFailed.svg'
    default: return 'images/sample.png'
  }
}

type Props = {
  info: BlockInfoProp,
  status: string
}

export default HeaderResult