import { IconButton, Typography } from '@mui/material'
import RadioButton from 'components/RadioButton'
import { FlexBox } from 'components/styled'
import React, { useState } from 'react'
import { useTranslation } from 'react-i18next'

function SelectService() {
  const {t} = useTranslation()
  const [values, setValues] = useState<Array<string>>([])
  const handleSelect = (value: string) => {
    const temp = values.filter(e => e !== value)
    if(temp.length !== values.length){
      setValues(temp)
    }
    else{
      setValues([value, ...values])
    }
  }
  return (
    <FlexBox flexWrap='wrap'>
      <Item
        value='deposit'
        selected={values.includes('deposit')}
        label={t('history.deposit')}
        onSelect={handleSelect}
      />
      <Item
        value='withdraw'
        selected={values.includes('withdraw')}
        label={t('history.withdraw')}
        onSelect={handleSelect}
      />
      <Item
        value='transfer'
        selected={values.includes('transfer')}
        label={t('history.transfer')}
        onSelect={handleSelect}
      />
      <Item
        value='payment'
        selected={values.includes('payment')}
        label={t('history.payment')}
        onSelect={handleSelect}
      />
    </FlexBox>
  )
}

const Item = ({value, selected, onSelect, label}: ItemProps) => {
  return(
    <IconButton
      onClick={() => onSelect?.(value)}
      sx={{
        width: '50%',
        display: 'flex',
        justifyContent: 'flex-start',
        alignItems: 'center'
      }}
    >
      <RadioButton isSelected={selected} />
      <FlexBox pr={1} />
      <Typography variant='subtitle1' color='text.primary'>{label}</Typography>
    </IconButton>
  )
}

type ItemProps = {
  value: string,
  label: string,
  selected: boolean,
  onSelect?: (value: string) => void
}

export default SelectService