import React from 'react'
import {FlexBox, FullSizeFlexBox, ScrollableFlexBox} from '../../../components/styled';
import Header from '../../../components/Layout/Header';
import {LayoutSafeArea} from '../../../components/Layout/LayoutSafeArea';
import {useTranslation} from 'react-i18next';
import {useNavigate} from 'react-router-dom';
import {useAppSelector} from '../../../app/hooks';
import {selectHistoryDetails} from '../states/history.slice';
import Image from 'mui-image';
import {Typography} from '@mui/material';
import KeyValueRow from '../components/KeyValueRow';

export const HistoryDetailScreen = () => {
  const { t } = useTranslation()
  const navigate = useNavigate()
  const detail = useAppSelector(selectHistoryDetails)

 return (
   <LayoutSafeArea>
     <FullSizeFlexBox
       flexDirection='column'
       overflow='hidden'
     >
       <FlexBox
         flexDirection='column'
         pl={2.5}
         pr={2.5}
       >
         <Header
           isIconBack
           title={t('history.historyDetailHeader')}
           onClick={() => navigate(-1)}
         />
       </FlexBox>

       <FlexBox
         pt={2.5}
       />
       <ScrollableFlexBox
         pl={2.5}
         pr={2.5}
         width='100%'
         flexDirection={'column'}
       >
         {detail?.blockInfo?.map((item, index) => {
           return (
             <FlexBox pb={2.5} alignItems={'center'} flexDirection={'column'} key={index}>
               <Image
                 src="images/icons/withdrawIcon.svg"
                 width={80}
                 height={80}
                 fit="contain"
                 duration={0}
                 bgColor='transparent'
               />
               <Typography pb={0.5} pt={2.5} sx={{fontSize: 14, fontWeight: 600}}>{item?.title}</Typography>
               <Typography sx={{fontSize: 24, fontWeight: 600}}>{item?.description}</Typography>
             </FlexBox>
           )
         })}
         {(detail?.transaction || []).length > 0  &&
           <FlexBox padding={'0 20px'} flexDirection={'column'} borderRadius={2.5} width={'100%'} border={'1px #E8E9EB solid'} >
             {detail?.transaction?.map((item, index) => {
               return (
                 <KeyValueRow key={item?.leftTitle} kei={item?.leftTitle} value={item?.rightTitle} valueColor={item?.rightColor} isLast={index + 1 === detail?.transaction?.length}/>
               )
             })}
           </FlexBox>}
         {(detail?.info || []).length > 0  &&
           <FlexBox flexDirection={'column'} width={'100%'}>
             <Typography sx={{fontSize: 16, fontWeight: 700, padding: '20px 0'}}>{t('cash-result.additionalInfo')}</Typography>
             <FlexBox padding={'0 20px'} flexDirection={'column'} borderRadius={2.5} border={'1px #E8E9EB solid'} >
               {detail?.info?.map((item, index) => {
                 return (
                   <KeyValueRow key={item?.leftTitle} kei={item?.leftTitle} value={item?.rightTitle} valueColor={item?.rightColor} isLast={index + 1 === detail?.transaction?.length}/>
                 )
               })}
             </FlexBox>
           </FlexBox>
         }
       </ScrollableFlexBox>
     </FullSizeFlexBox>
   </LayoutSafeArea>
 )
}
