import { IconButton, Typography } from '@mui/material'
import dayjs from 'dayjs'
import Image from 'mui-image'
import React from 'react'

function CalenderButton({date, onClick, selectedId, id}: CalenderProps) {
  return (
    <IconButton
      sx={{
        display: 'flex',
        flex: 1,
        justifyContent: 'space-between',
        p: 1.5,
        pt: 1.25,
        pb: 1.25,
        borderRadius: 2,
        border: '1px solid',
        borderColor: selectedId === id ? 'primary.main' : 'colors.gray',
      }}
      onClick={onClick}
    >
      <Typography
        sx={{
          fontSize: 14,
          fontWeight: '400',
          color: 'text.primary'
        }}
      >{dayjs(date).format('DD/MM/YYYY')}</Typography>
      <Image src='images/icons/calendarIcon.svg' width={24} height='auto' fit='cover' duration={0} />
    </IconButton>
  )
}

export type CalenderProps = {
  date: Date,
  onClick?: () => void
  id?: string,
  selectedId?: string
}

export default CalenderButton