import {IconButton, List} from '@mui/material'
import Header from 'components/Layout/Header'
import { LayoutSafeArea } from 'components/Layout/LayoutSafeArea'
import { ModalHandle } from 'components/Modal/types'
import SearchInput from 'components/SearchInput'
import { FlexBox, FullSizeFlexBox, ScrollableFlexBox } from 'components/styled'
import Image from 'mui-image'
import React, { useRef } from 'react'
import { useTranslation } from 'react-i18next'
import { useNavigate } from 'react-router-dom'
import FilterModal from '../components/FilterModal'
import HistoryItem from '../../home/components/HistoryItem'
import {selectHistoryList} from '../states/history.slice';
import {useAppDispatch, useAppSelector} from '../../../app/hooks';
import {HistoryItemData} from '../states/history.types';
import {historySagaAction} from '../states/history.saga';
import alertDialogStore from '../../../components/AlertDialog/alertDialogStore';

function HistoryScreen() {
  const { t } = useTranslation()
  const dispatch = useAppDispatch()
  const navigate = useNavigate()
  const searchRef = useRef()
  const filterRef = useRef<ModalHandle>(null)
  const list = useAppSelector(selectHistoryList)

  const _onItem = (item: HistoryItemData) => {
    dispatch(historySagaAction.getHistoryDetails({transaction: item?.transaction }, (status) => {
      if (status) {
        navigate('/history-detail')
      } else {
        alertDialogStore.alertContent = {
          title: 'Lấy thông tin thất bại',
          subtitle: 'Lấy thông tin giao dịch thất bại. Vui lòng thử lại sau',
          firstButtonTitle: 'Thử lại',
          secondButtonTitle: 'Trở về',
          reverseButton: true
        }
      }
    }))
  }

  return (
    <LayoutSafeArea>
      <FullSizeFlexBox
        flexDirection='column'
        overflow='hidden'
      >
        <FlexBox
          flexDirection='column'
          pl={2.5}
          pr={2.5}
        >
          <Header
          isIconBack
          title={t('history.header')}
          onClick={() => navigate(-1)}
           />
          <FlexBox
            pt={1.5}
          />
          <FlexBox
            width='100%'
          >
            <SearchInput
              ref={searchRef}
              placeholder={t('history.search')}
            />
            <IconButton
              sx={{ marginLeft: 1 }}
              onClick={() => filterRef.current?.open?.()}
            >
              <Image src='images/icons/filterIcon.svg' width={18} height='auto' fit='cover' duration={0} />
            </IconButton>
          </FlexBox>
        </FlexBox>
        <ScrollableFlexBox>
          <List sx={{ width: '100%' }}>
            {list?.map((item, index) => <HistoryItem onPress={() => _onItem(item)} item={item} key={index} beforeItemDate={list?.[index - 1]?.publishedAt} />)}
          </List>
        </ScrollableFlexBox>
        {/*<EmptyHistory />*/}
        <FilterModal
          ref={filterRef}
        />
      </FullSizeFlexBox>
    </LayoutSafeArea>
  )
}

export default HistoryScreen
