import { Box, Zoom } from '@mui/material';
import useTheme from 'app/theme';
import React from 'react';
// import checkedIcon from './Images/checkedIcon'
import selectedIcon from './Images/selectedIcon';

function RadioButton({ size = 24, isSelected=true }: RadioButtonProps) {
  const themes = useTheme()
  return (
    <Box
      width={size} height={size}
      borderRadius='50%'
      border= {isSelected ? 'none' : '1px solid'}
      borderColor='text.buttonDisable'
    >
      <Zoom in={isSelected} timeout={300}>
      {selectedIcon(themes)}
      </Zoom>
    </Box>
  );
}

type RadioButtonProps = {
  size?: number;
  isSelected?: boolean;
};

export default RadioButton;
